<template>
  <v-data-table v-bind="$attrs" v-on="$listeners">
      <template v-slot:top="options">
        <slot name="top" v-bind="options"></slot>
      </template>
      
      
      <template v-slot:item="{ headers, item, isMobile, isSelected, select }" >
                <tr :class="{'v-data-table__mobile-table-row': isMobile}" @click="$emit('click:row', item)">
                    <td 
                    v-for="header in headers" 
                    :key="header.value" 
                    :width="!isMobile ? header.width : null"
                    :class="[{'v-data-table__mobile-row': isMobile},{'v-data-table__divider': !isMobile}, {[`text-${header.align || 'start'}`]: !isMobile}, ...header.class]"
                    >
                        <div :class="{'v-data-table__mobile-row__header': isMobile}" v-if="isMobile">{{header.text}}</div>
                        
                        <div v-if="header.value == 'data-table-select'"  :class="{'v-data-table__mobile-row__cell': isMobile}" >
                            <VSimpleCheckbox :ripple="false" class="v-data-table__checkbox" :value="isSelected" @input="select" />
                        </div>
                        <div  v-else-if="header.value == 'actions'"   :class="{'v-data-table__mobile-row__cell': isMobile}" >
                            <v-tooltip bottom v-for="(btn, ind) in header.buttons" :key="ind" >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn icon
                                        v-bind="typeof btn.bind == 'function' ? btn.bind(vm, item) : btn.bind"
                                        v-on="typeof btn.on == 'function' ? btn.on(vm, item) : btn.on"
                                    >

                                        <v-icon
                                        v-bind="attrs" 
                                        v-on="on"
                                        small
                                      
                                        >
                                        {{ btn.icon }}
                                        </v-icon>
                                    </v-btn>
                                </template>
                                <span>{{btn.title}}</span>
                            </v-tooltip>
            
                        </div>
                        <div v-else-if="header.component"  :class="{'v-data-table__mobile-row__cell': isMobile}" >
                            
                            <component 
                            :is="header.component.is || header.component" 
                            v-bind="typeof header.component == 'function' ? {} : typeof header.component.bind == 'function' ? header.component.bind(vm, item) : (header.component.bind || {})"
                            v-on="typeof header.component.on == 'function' ? header.component.on(vm, item) : header.component.on"
                            :item="item" 
                            :header="header"
                            ></component>

                        </div>

                        <div v-else  :class="{'v-data-table__mobile-row__cell': isMobile}"  v-html="$options.filters.dynamic($getNestedValue(item, header.value), header.format, getFormatArgs(header.formatArgs, item) || [])"></div>

                    </td>
                </tr>
      </template>
  </v-data-table>
</template>
<script>
 

export default {
  name: 'data-table',
  computed: {
    vm () {
            return this
        },
  }
  
}
</script>